/* header */
.fubrl{
  margin-top: 10px;
}
.headermain{
  padding-top: 10px;
  height: 15vh;
  width: 100%;
  /* display: block; */
  display: flex;
  background-color: white;
  align-items: center;
  color: crimson;
  font-weight: bold;
  font-size: large;
  border-bottom: 1px solid gray;
  box-shadow: 3px 3px 5px gray ;
}
.div1h{
  width: 50%;
 display: flex;
  /* text-align: left; */
  
  cursor: pointer;
  /* font-size: 25px */
}
.div1h p{
  width: 15%;
  text-align: right;
  padding: 5px;
}
.div1h img{
  height:70px;
  width: 200px;
  /* width: 85%; */
  padding-left: 30px;
}


.div2h{
  width: 50%;
}
.div2h ul{
  
  display: flex;
  justify-content: space-evenly;
  list-style: none;
  cursor: pointer;
}


.li:hover {
  background-color: gray;
  color: white;
  /* font-weight: bolder; */
  cursor: pointer;
  border-radius: 5px;
  /* height: 30px;
  width: 100px; */
  text-align: center;
}

/* Optionally, you can add transition for a smooth effect */
.li {
  transition: all ease-in-out .2s ;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  padding-top: 100px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 60%;
}

.close {
  color: white;
  float: right;
  font-size: 18px;
  font-weight: bold;
  border-radius: 5px;
  height: 50px;
  width: 50px;
  background-color: red;
  border: none;

}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

.mymodal{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.input{
  width: 100%;
  margin-bottom: 5px;
}

/* footer */

.footer{
  height: 15vh;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-size: large;
  font-weight: bold;
  color: #585353;
}
.span {
  font-size: xx-large;
}
.link{
text-decoration: none;
color: #585353;
}

/* homepage  */

.homemain{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 100px;
 
  
}
.fari{
  font-size: 50px;
  font-weight: 900;
  color: crimson;
}
.lines{
font-weight: 400;
color: rgb(44, 42, 42);
}
.btn{
  background-color: crimson;
  height: 50px;
  width: 200px;
  font-weight: 500;
  color: white;
  box-shadow: 3px 3px 10px #555;
  }
 

  .banner{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 50px;
    
  }
  .bannerimg{
    border-radius: 10px;
    box-shadow: 5px 5px 20px #555555ac;
  }
  .hrr{
    display: flex;
    flex-direction: column;
    align-items: center;
    /* margin-top: 100px; */
    color: black;
    font-weight: bold;
  }
  .hr{
    width: 100px;
    border: 2px solid black;
    background-color: black; 
  }

  .gallery{
    display: flex;
