  }

  .address{
    margin-top: 50px;
    display: flex;
    justify-content: space-around;
    /* align-items: center; */
  }
  .adp{
    font-size: 20px;
    font-weight: bold;
    text-align: left;
  }
  .call{
    cursor: pointer;
  }
 
 